@import url('https://fonts.googleapis.com/css2?family=Ysabeau:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ysabeau&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cinzel&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Jura&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');
*{
    box-sizing:border-box;
    margin:0;
    padding:0
}
.app{
    background:url('../../media/weather1.6ced1180542367f22f9d.webp');
    background-attachment:fixed;
    background-repeat:no-repeat;
    background-size:100% 100%;
    background-size:cover;
    height:100vh;
    position:relative
}
.app:after{
    background:#13131d56;
    bottom:0;
    content:"";
    left:0;
    position:absolute;
    right:0;
    top:0;
    z-index:2
}
.app-container{
    background:hsla(0,0%,96%,.555);
    border-bottom-right-radius:30px;
    border-top-left-radius:30px;
    box-shadow:0 0 15px 1px #2523385e;
    left:50%;
    max-height:500px;
    max-width:400px;
    padding:20px;
    position:fixed;
    top:50%;
    -webkit-transform:translate(-50%,-50%);
    transform:translate(-50%,-50%);
    width:100%;
    z-index:3
}
.app__note{
    color:#00000091;
    font-family:Ysabeau,sans-serif;
    font-size:16px;
    padding-bottom:5px;
    text-align:center
}
.gif{
    background-image:url('../../media/weather__gif.929333e51ffde2fd4016.gif');
    background-size:100% 100%;
    border-bottom-right-radius:20px;
    height:180px;
    padding-bottom:20px;
    width:360px
}