@import url('https://fonts.googleapis.com/css2?family=Ysabeau:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ysabeau&display=swap');
.search-form{
    align-items:center;
    display:flex;
    width:100%
}
input::-webkit-input-placeholder{color:#000000bb}
input::placeholder{color:#000000bb}
.search-form__btn{
    background:#42819e;
    border:0;
    border-bottom-right-radius:20px;
    color:#f0f8ff;
    font-family:Ysabeau,sans-serif;
    font-size:large;
    padding:8px 0 12px;
    width:30%
}
.search-form__btn:hover{
    background:#53a2c7
}