@import url('https://fonts.googleapis.com/css2?family=Ysabeau:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ysabeau&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cinzel&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Jura&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');

.weather__main-info{
    font-family:Jura,sans-serif;
    font-size:larger
}
.weather__name{
    font-size:40px;
    font-weight:bolder
}
.weather__desc{
    font-weight:500;
    padding-top:15px
}
.weather__temp{
    display:flex;
    font-size:29px;
    font-weight:600;
    margin-left:-8px;
    margin-top:-15px
}
.weather__temp-num{
    margin-left:-10px;
    padding-top:40px
}
.weather__secondary{
    color:#0000007c;
    display:grid;
    font-size:20px;
    font-weight:600;
    grid-template-columns:1fr 1fr;
    position:relative;
    width:100%
}
.weather__secondary:before{
    background:#00000069;
    content:"";
    height:2px;
    position:absolute;
    width:100%
}
.weather__secondary-col{
    color:#0000009a;
    font-size:large;
    font-weight:700;
    padding-top:10px;
    text-transform:uppercase
}